.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown-button {
    padding: 10px;
    cursor: pointer;
}

.dropdown-menu {
    width: 184px;
    height: 130px;
    font-size: 16px;
    position: absolute;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 12px;
    list-style-type: none;
    padding: 0;
    margin: 0;
    z-index: 1;
}

.dropdown-menu li {
    padding: 10px;
    cursor: pointer;
}

.dropdown-menu li:hover {
   color: #22a0bc
}
