body {
  margin: 0;
  font-family: "Manrope", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f4f4f4;
}
.main
{
  width: 100%;
}
.data
{  
  
  margin-left: auto;
  margin-right: auto;
  width: 90%;
