.profileData
{
    width: 760px;
    height: 813px;
    border-radius: 16px;
    box-sizing: border-box;
    background-color: #FDFDFD;
    padding: 40px;
}
.bold
{
    font-weight: 600;
}
.profile-caption
{
    position: relative;
    width:680px;
    height: 49px;
    font-size: 24px;
    margin-bottom: 32px;
}
.small-divider {
    position: absolute;
    bottom: 0;
    width: 100%; /* Ширина линии */
    height: 1px; /* Высота линии */
    background-color: #dadada; /* Цвет линии */
    /*margin: 20px 0;  Отступы сверху и снизу */
}
.profile-input
{
    margin-top: 24px;
    width: 420px;
    height: 77px;
}
.input-label
{
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 8px;
}
input
{
    box-sizing: border-box;
    padding: 16px;
    height: 42px;
    width: 420px;
    border-radius: 50px;
    border: 1px solid #dadada;
    font-size: 16px;
    color:#161616;
}
button
{
    margin-top: 32px;
    color: #fdfdfd; /* Цвет текста */
    background-color: #161616; /* Цвет заднего фона */
    border: none; /* Убираем границу */
    border-radius: 50px; /* Округление углов */
    padding: 12px 42px; /* Отступы внутри кнопки */
    font-size: 16px; /* Размер шрифта */
}