.card {
    width: 360px;
    height: 168px;
    padding: 20px 0 0 20px;
    border-radius: 16px;
    box-sizing: border-box;
    background-color: #FDFDFD;
    margin-right: 40px;
    margin-bottom: 32px;
}
.info
{
    position: relative;
    width: 180px;
    height: 120px;
    margin-left: 20px;
}
.username
{
    position: relative;
    width: 180px;
    /*height: 27px;*/
}
.accent 
{
    width: 140px;
  color: #22A0BC;
  font-size: 20px;
  font-weight: 600;
}
.dropdown-container
{
    width: 24px;
    height: 24px;
    position: absolute;
    right: 0;
}
.city
{
    width: 180px;
    height: 19px;
    position: absolute;
    bottom: 0;
}

