.top
{
    display: flex;
    justify-content: center; /* Центрирование по горизонтали */
    align-items: center; /* Центрирование по вертикали */
    width: 100%;
    height: 56px;
    background-color: #fdfdfd;
}
.topContent
{
    position: relative;
    width: 90%;
    height: 24px;
}
.registeredMenu
{
    position: absolute;
    right: 0;
    width: 169px;
    height: 100%;
}
.nameFoto
{
    position: absolute;
    right: 0;
    
    width: 93px;
    height: 100%;
}
.radius-50 img
{
    border-radius: 50px;
}
.align-vertically
{
    display: flex;
    align-items: center;
}
.user
{
    margin-left: 8px;
    width: 16px;
    height: 100%;
}
.notification
{
    width: 20px;
    height: 100%;
    margin-left: 4px;
}
.favourite
{
    width: 20px;
    height: 100%;
}